'use client';

import { useEthereumStats } from '@/utils/useEtherscanData';
import { Activity, Flame, Box, Zap } from 'lucide-react';

export function StatsGrid() {
  const { data: stats, isLoading } = useEthereumStats();

  if (isLoading) {
    return (
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {[...Array(4)].map((_, i) => (
          <div
            key={i}
            className="h-32 bg-gray-200 animate-pulse rounded-xl"
          ></div>
        ))}
      </div>
    );
  }

  const statCards = [
    {
      title: 'Transactions',
      value: stats?.totalTransactions || '0',
      subtitle: `${stats?.tps || 0} TPS`,
      icon: Activity,
      color: 'bg-blue-500',
    },
    {
      title: 'Med Gas Price',
      value: `${stats?.gasPrice || 0} Gwei`,
      subtitle: '< $0.01',
      icon: Flame,
      color: 'bg-orange-500',
    },
    {
      title: 'Last Finalized Block',
      value: stats?.lastFinalizedBlock.toLocaleString() || '0',
      subtitle: 'Finalized',
      icon: Box,
      color: 'bg-green-500',
    },
    {
      title: 'Last Safe Block',
      value: stats?.lastSafeBlock.toLocaleString() || '0',
      subtitle: 'Safe',
      icon: Zap,
      color: 'bg-purple-500',
    },
  ];

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
      {statCards.map((stat) => (
        <div key={stat.title} className="bg-white p-6 rounded-xl shadow">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-gray-500 text-sm">{stat.title}</p>
              <p className="text-2xl font-bold mt-2">{stat.value}</p>
              <p className="text-gray-400 text-sm mt-1">{stat.subtitle}</p>
            </div>
            <div className={`${stat.color} p-3 rounded-full`}>
              <stat.icon className="w-6 h-6 text-white" />
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}
