import axios from 'axios';

const ETHERSCAN_API_KEY = process.env.NEXT_PUBLIC_ETHERSCAN_API_KEY || 'demo';

export const etherscanApi = axios.create({
  baseURL: process.env.NEXT_PUBLIC_ETHERSCAN_BASE_URL,
  params: {
    apikey: ETHERSCAN_API_KEY,
  },
});

export interface EthereumStats {
  ethPrice: number;
  priceChange: number;
  totalTransactions: string;
  gasPrice: number;
  lastFinalizedBlock: number;
  lastSafeBlock: number;
  tps: number;
}

export interface Transaction {
  hash: string;
  from: string;
  to: string;
  value: string;
  timestamp: number;
  gasPrice: string;
}

export interface Block {
  number: number;
  timestamp: number;
  miner: string;
  transactions: string[];
  gasUsed: string;
  gasLimit: string;
}
